import React, { useEffect, useState } from "react";
import Loader from "../../../common/loader/Loader";
import { Select as AntSelect, message } from "antd";
import {
  getRetailersForASMZSM,
  getbbpsReportforASMASM,
  enquirybbpsforASMASM,
  getbbpsExcelReportforASMASM,
} from "../../../api/login/Login";
import {
  Pagination,
  Autocomplete,
  Box,
  TextField,
  Typography,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import * as XLSX from 'xlsx'; 
import { useReportDates } from "../../../utils/getDate";

const AsmBbpsReport = ({title}) => {
  const today = new Date().toISOString().split("T")[0];

  const [loading, setLoading] = useState(false);
   const { startDate, endDate } = useReportDates();

  const [selectedRow, setSelectedRow] = useState(null);
  const [page, setPage] = useState(1);
  const [retailers, setRetailers] = useState([]);
  const [selectedRetailer, setSelectedRetailer] = useState(null);
  const [totalCount, setTotalCount] = useState(0);
  const [submit, setSubmit] = useState(false);
  const [data, setData] = useState([]);

  const [rowsPerPage, setRowsPerPage] = useState(10);

  const [filter, setFilter] = useState({
    // customer_mobile: "",
    response: "",
    start_date: startDate,
    end_date: endDate,
    txn_id: "",
    service_name: "",
  });

  const statusOptions = [
    { value: "", label: "All" },
    { value: "1", label: "Pending" },
    { value: "2", label: "Success" },
    { value: "3", label: "Failed" },
    { value: "4", label: "Refunded" },
  ];

//   const bankOptions = [
//     { value: "", label: "All" },
//     { value: "bank1", label: "Bank 1" },
//     { value: "bank2", label: "Bank 2" },
//   ];

  const getRetailers = async () => {
    try {
      let res = await getRetailersForASMZSM();
      if (res?.data?.error === false) {
        setRetailers(res.data?.data || []);
      } else {
        setRetailers([]);
      }
    } catch (err) {
      console.error(err);
      setRetailers([]);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFilter((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleStatusChange = (value, key) => {
    setFilter((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const handleSearch = () => {
    setPage(1)
    setSubmit((prev) => !prev);
  };

  const ResetData = () => {
    setFilter({
        response: "",
        start_date: '',
        end_date: '',
        txn_id: "",
        service_name: "",
    });
    setSelectedRetailer(null);
    setSubmit((prev) => !prev);
    setRowsPerPage(10);
    setPage(1)
  };

  const handleChangeRowsPerPage = (e) => {
    setRowsPerPage(e.target.value);
    setPage(1);
  };

  const handleChangePage = (event, value) => {
    setPage(value);
  };

//   const handleViewClick = (item) => {
//     setSelectedRow(item);
//   };

  useEffect(() => {
    getRetailers();
  }, []);

  const getData = async () => {
    setLoading(true);
    try {
      const res = await getbbpsReportforASMASM({
        ...filter,
        retailer_id: selectedRetailer?._id || "",
        page,
        limit: rowsPerPage,
      });
      if (res?.data?.error === false) {
        setData(res.data?.data?.bbpsAllTxns || []);
        setTotalCount(res.data?.data?.totalTxns || 0);
      } else {
        setData([]);
        setTotalCount(0);
        message.error(res.data?.message || "Something went wrong");
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
  }, [submit, page, rowsPerPage]);

  const responseEnquiry = async (id) => {
    setLoading(true);
    try {
      let res = await enquirybbpsforASMASM(id);
      if (res?.data?.error === false) {
        message.success(res.data?.message);
        setSubmit((prev) => !prev);
      } else {
        message.error(res.data?.message);
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };


  const downloadExcel = async () => {
    try {
      setLoading(true);
      const res = await getbbpsExcelReportforASMASM({
        ...filter,
        retailer_id: selectedRetailer?._id || "",
      });

      if (res?.data?.data?.bbpsAllTxns?.length > 0) {
        const dataForExcel = res?.data?.data?.bbpsAllTxns.map((item, i) => ({
            "Sr. No": i + 1,
            "Date / Time": item?.createdAt || "",
            "Transaction ID": item?.txn_id || "",
            "Operator Name": item?.operator_name || "",
            "Amount": item?.amount || "",
            "Status":
              item?.status === 2 ? "Success" :
              item?.status === 1 ? "Pending" :
              item?.status === 3 ? "Failed" :
              item?.status === 4 ? "Refund" :
              "Unknown",
            "CA Number": item?.ca_num || "",
            "Opening Balance": item?.open_bal || "",
            "Closing Balance": item?.close_bal || "",
            "Ref ID": item?.refId || "",
            "Biller Id ID": item?.biller_id || "",
            "Charges": item?.charges || "",
            "Service Name": item?.service_name || "",
            "Created By Name": item?.CreatedBy_name || "",
            "Created By Mobile": item?.CreatedBy_mobile || "",
            "Created By Refer ID": item?.CreatedBy_refer_id || "",
            "Retailer Name": item?.retailer_name || "",
            "Retailer Mobile": item?.retailer_mobile || "",
            "Retailer Refer ID": item?.retailer_refer_id || "",
            "Status Update Time": item?.status_update_time || ""
          }));
          

        const ws = XLSX.utils.json_to_sheet(dataForExcel);
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "BBPS Report");
        XLSX.writeFile(wb, `BBPS_Report_${new Date().toLocaleDateString()}.xlsx`);
      } else {
        message.error('No data available to export!');
      }
    } catch (error) {
      message.error('Failed to download Excel. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const totalPages = Math.ceil(totalCount / rowsPerPage);

  return (
    <>
      {loading && <Loader />}
      {title && (
      <h3
        className="mt-3 mb-3"
        style={{
          color: "var(--bs-primary2)",
          fontWeight: 700,
          borderBottom: "1px solid var(--bs-primary2)",
          paddingBottom: "8px"
        }}
      >
        {title}
      </h3>
    )}
      <div className="card">
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="start_date"
                  className="form-control"
                  max={today}
                  value={filter.start_date}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="end_date"
                  className="form-control"
                  max={today}
                  value={filter.end_date}
                  onChange={handleChange}
                />
              </div>
            
             
              <div className="form-group col-md-4">
                <label>Txn Id</label>
                <input
                  type="text"
                  name="txn_id"
                  className="form-control"
                  placeholder="Enter Txn Id"
                  value={filter.txn_id}
                  onChange={handleChange}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Service Name</label>
                <input
                  type="text"
                  name="service_name"
                  className="form-control"
                  placeholder="Enter Service Name"
                  value={filter.service_name}
                  onChange={handleChange}
                />
              </div>
            
              <div className="form-group col-md-4">
                <label>Status</label>
                <AntSelect
                  style={{ width: "100%" }}
                  value={filter.response}
                  onChange={(value) => handleStatusChange(value, "response")}
                  options={statusOptions}
                />
              </div>
              <div className="form-group col-md-4">
                <label>Select Retailer</label>
                <Autocomplete
                  options={retailers}
                  getOptionLabel={(option) => option.name || ""}
                  value={selectedRetailer ?? null}
                  size="small"
                  onChange={(e, newVal) => setSelectedRetailer(newVal)}
                  renderOption={(props, option) => (
                    <li {...props} key={option.retailer_id}>
                      <Box sx={{ fontSize: "13px", color: "var(--bs-nav)" }}>
                        {option.name}
                      </Box>
                    </li>
                  )}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Select Retailer"
                      InputLabelProps={{
                        sx: {
                          "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                            top: "-3px",
                          },
                        },
                      }}
                      sx={{
                        "& label.Mui-focused": {
                          color: 'var(--bs-nav)',
                        },
                        "& .MuiInputBase-root": { backgroundColor: "#fff" },
                        "& .MuiOutlinedInput-root": {
                          "&:hover fieldset": {
                            borderColor: "var(--bs-primary1)",
                          },
                          "&.Mui-focused fieldset": {
                            borderColor: "var(--bs-primary1)",
                          },
                        },
                      }}
                    />
                  )}
                />
              </div>
              <div className="form-group col-md-12">
                <button
                  type="button"
                  className="btn btn-primary mr-3"
                  onClick={handleSearch}
                >
                  Search
                </button>
                <button
                  type="button"
                  className="btn btn-light"
                  onClick={ResetData}
                >
                  Reset
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
      <div className="card mt-4" style={{ overflow: "auto" }}>
        <div className="card-body">
          <div id="myTable_wrapper" className="dataTables_wrapper no-footer">
            <div className="d-flex  justify-content-end align-items-center">
              <div className="dataTables_length mb-3" id="myTable_length">
                <Button
                  className="btn btn-success cusxel"
                  onClick={downloadExcel}
                  // disabled={loading}
                >
                  Download Excel sheet
                </Button>
             
              </div>
            </div>
         
            <div className="table-responsive">
              <table
                className="table table-striped table-bordered table-hover display dataTable no-footer dtr-inline collapsed"
                role="grid"
                aria-describedby="myTable_info"
              >
                <thead>
                  <tr role="row">
                    <th className="">S.No</th>
                    <th className="">Txn Date</th>
                    <th className="">Operator Name</th>
                    <th className="">CA Number</th>
                    <th className="">Txn Id</th>
                    <th className="">Service Name </th>
                    <th className="">Opening Balance</th>
                    <th className="">Amount</th>
                    <th className="">Closing Balance</th>
                    <th className="">Txn Status</th>
                    {/* <th className="">View</th> */}
                    {/* <th className="">Service Name</th> */}
                    <th className="">Retailer Name</th>
                    <th className="">Retailer Code</th>
                    <th className="">Retailer Number</th>
                    <th className="">Status Update At</th>
                    <th className="">Enquiry</th>
                    <th className="">Invoice</th>
                    {/* <th className="sorting">Dispute</th> */}
                  </tr>
                </thead>
                <tbody>
                  {data?.map((item, i) => (
                    <tr className="odd" key={item?._id}>
                      <td valign="top" className="dataTables_empty">
                      {i + 1 + ((page-1) * rowsPerPage)}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.createdAt}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.operator_name}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?. ca_num}
                      </td>
                     
                      <td valign="top" className="dataTables_empty">
                        {item?.txn_id || "---"}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.service_name}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.open_bal}
                      </td>

                      <td valign="top" className="dataTables_empty">
                        {item?.amount}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.close_bal}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.status == 1 ? (
                          <button
                            className="btn btn-warning"
                            style={{ width: "130px" }}
                          >
                            Pending
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.status == 2 ? (
                          <button
                            className="btn btn-success"
                            style={{ width: "130px" }}
                          >
                            Success
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.status == 3 ? (
                          <button
                            className="btn btn-danger"
                            style={{ width: "130px" }}
                          >
                            Failed
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.status == 4 ? (
                          <button
                            className="btn btn-light"
                            style={{ width: "130px" }}
                          >
                            Refunded
                          </button>
                        ) : (
                          ""
                        )}
                        {item?.status == 5 ? (
                          <button
                            className="btn btn-light"
                            style={{ width: "130px" }}
                          >
                            Not Initiated
                          </button>
                        ) : (
                          ""
                        )}
                      </td>
                     
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_name}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_refer_id}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.retailer_mobile}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        {item?.status_update_time}
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Button
                          onClick={() => responseEnquiry(item?.txn_id)}
                          disabled={
                            loading || item?.status !== 1 
                          }
                        >
                          Enquiry
                        </Button>
                      </td>
                      <td valign="top" className="dataTables_empty">
                        <Link
                          className="btn btn-primary"
                          to={`/bill-invoice/${item?._id}`}
                        >
                          Invoice
                        </Link>
                      </td>
                      {/* <td valign="top" className="dataTables_empty">
                        <Link
                          className="btn btn-primary"
                          to={`/add-ticket/${item?._id}`}
                          state={{
                            item,
                            serviceId: "65f9484a26eb74e182c640fc",
                          }}
                        >
                          Dispute
                        </Link>
                      </td> */}
                    </tr>
                  ))}

                  {data?.length === 0 && (
                    <td
                      style={{ textAlign: "center" }}
                      colSpan={14}
                      valign="top"
                      className="dataTables_empty"
                    >
                      No Record Found
                    </td>
                  )}
                </tbody>
              </table>
            </div>
          </div>
          {/* </div> */}
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
            flexWrap="wrap"
            gap={2}
          >
            <Box display="flex" alignItems="center" gap={2}>
              <FormControl size="small" sx={{ width: "150px" }}>
                <InputLabel>Rows per page</InputLabel>
                <Select
                  value={rowsPerPage}
                  onChange={handleChangeRowsPerPage}
                  label="Rows per page"
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
                >
                  {[5, 10, 25, 50].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <Typography
                variant="body2"
                sx={{ fontSize: "14px", color: "#555" }}
              >
                Total Entries: {totalCount}
              </Typography>
            </Box>
            <Pagination
              count={totalPages}
              page={page}
              onChange={handleChangePage}
              shape="rounded"
              showFirstButton
              showLastButton
              sx={{
                "& .MuiPaginationItem-root": {
                  color: "var(--bs-primary1)",
                  borderRadius: "8px",
                },
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "var(--bs-primary1)",
                  color: "#fff",
                  fontWeight: "bold",
                  "&:hover": { backgroundColor: "var(--bs-primary1)" },
                },
              }}
            />
          </Box>
        </div>
      </div>

      {/* ///Show PopUP */}
     
    </>
  );
};

export default AsmBbpsReport;
